
import React from "react";

// Buttons
const importAll = (r) => {
  return r.keys().map(r);
};

const images = importAll(require.context('../img/button-gifs', false, /\.(png|jpe?g|gif)$/));

function Images(img) {
    return <img src={img} alt="Button Gif" />
}

export default function Buttons() {

    return <div id="button-container">
        {images.map(Images)}
    </div>
}