import { Link } from "react-router-dom/cjs/react-router-dom.min"
import "./../css/sideColumn.css"
import pagesJSON from "../json/pages.json"
import Buttons from "./ButtonGifs";
import MusicPlayer from "./MusicPlayer";


// navbarElements template:
// {name: "Home", address: "/", type: parent, clickable: clickable
//     },


const navbarElements = [
    {name: "Home", address: "/", level:0, clickable: true
    },
    {name: "Practice", address:"/practice", level:0, clickable: true
    }
]

function renderElement(e){
    let element;

    let style = {
        paddingLeft: (1.5 + e.level) + "vw",
        fontSize: (e.level === 0)? "x-large" : "large",
        fontWeight: (e.level === 0) ? "bolder" : "normal"
    }

    if(e.clickable){
        element = <Link to={e.address} className="navbar-entry link" style={style}> <p>{e.name}</p></Link>
    } else {
        element = <div className="navbar-entry" style={style}> <p>{e.name}</p></div>
    }

    return element;
}

export function LeftColumn(){
    return (
        <div className="side-column">
            {/* <Logo /> */}
            {navbarElements.map(renderElement)}
        </div>
    )
    
}

export function RightColumn(){

    return (
        <div className="side-column" id="right-column">
            <MusicPlayer />
            
            <Buttons />
    </div>
    )
    
}