import React from "react";
import { useState } from "react";
import { reactLocalStorage } from "reactjs-localstorage";

export default function PracticeCounter() {
    
    const [counter, setCounter] = useState(0);

    const addToCounter = () => {
        let currentCounter = getCount();
        currentCounter++;
        setCounter(currentCounter);
        reactLocalStorage.set("count", currentCounter);

    }

    const resetCounter = () => {
        let currentCounter = getCount();
        currentCounter = 0;
        setCounter(currentCounter);
        reactLocalStorage.set("count", currentCounter);
    }

    const getCount = () => {
        return reactLocalStorage.get("count",0,true)
    }


    return <div>
        <h1>Counter</h1>
        <p>Uses useState() to add 1 to the counter, and reactLocalStorage. set()/get() to save and retrieve from local storage, maintaining it through reloads </p>
        <h1 style={{ textAlign: "center" }}>{getCount()}</h1>
        <button onClick={() => { addToCounter() }}>Add 1</button>
        <button onClick={()=>{resetCounter()}}>Reset</button>
    </div>
}