import React, {useState} from "react";
import practiceData from "../../json/practice.json"

export function PracticeJSON(){

    return <div>
        <h2>Json parsing</h2>

        <h4>Json Object</h4>
        {practiceData.map((e)=>{return <p>{e.author}: {e.name}</p>})}

        <br />

        <h4>Json String</h4>
        <p>{JSON.stringify(practiceData)}</p>
    </div>
}