import React, {useState} from "react";

export function PracticeColorChange(){

    const [currentColor, setColor] = useState("red");

    return <>
        <h2>useState: Color changer</h2>
        <div>
            <h1 style={{margin:'auto', backgroundColor:currentColor, color:"white", width:"10vw", textAlign:"center"}}> {currentColor} </h1>

            <button onClick={()=>{setColor("red")}}>Red</button>
            <button onClick={()=>{setColor("blue")}}>BLue</button>
            <button onClick={()=>{setColor("green")}}>Green</button>
            <button onClick={()=>{setColor("purple")}}>Purple</button>
        </div>
    </>
}