import React, { useState, useEffect } from "react";

import "../css/musicPlayer.css"

async function importSongData(song) {
  const audioModule = await import(`../audio/music/${song.src}`);
  const coverModule = await import(`../img/cover-images${song.coverSrc}`);
  return { ...song, src: audioModule.default, coverSrc: coverModule.default }; // Assuming default export for audio/images
}

function SongList() {
  const [songs, setSongs] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      const response = await fetch('songs.json');
      const data = await response.json();

      const processedSongs = await Promise.all(
        data.map(async (song) => await importSongData(song))
      );
      setSongs(processedSongs);
    };

    fetchData();
  }, []);

  // ... (Rest of your component logic using the 'songs' array)
}




