export default function MusicPlayer() {
    const [isPlaying, setIsPlaying] = useState(true);

    function Controls(){
        return <div id="music-controls">

            {/* Prev */}
            <div>⏪</div>

            {/* Pause/Play */}

            <div onClick={()=>{togglePlay()}}>{isPlaying ? "⏸":"⏵"}</div>

            {/* Next */}
            <div>⏩</div>
        </div>
    }

    function togglePlay() {
        setIsPlaying(!isPlaying);
    }
    

    return <div id="music-player">
        <Highlight />
        <Controls />
    </div>
}